* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

header {
  width: 100%;
  height: auto;
  background-color: #27282f;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

nav {
  height: 100px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  width: 50px;
  margin-left: 20px;
}

.bar-btn {
  color: #fff;
  cursor: pointer;
  margin-right: 20px;
  font-size: 25px;
}

.nav-menu {
  width: 100%;
  height: 0;
  text-align: center;
  background-color: #27282f;
  transition: all .5s;
  position: fixed;
  top: 100px;
}

.nav-menu li {
  margin: 50px 0;
  line-height: 30px;
  transition: all .5s;
  display: none;
}

.nav-menu li a {
  color: #fff;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 600;
}

.nav-menu li a:hover, .nav-menu li a.active {
  color: gold;
  transition: all .3s;
}

#check {
  display: none;
}

#check:checked ~ .nav-menu {
  height: 100vh;
  position: absolute;
}

#check:checked ~ .nav-menu li {
  display: block;
}

